<template>
    <div class="my-orders-box">
         <van-nav-bar 
        title="我的订单"
        left-text="返回"
        left-arrow
        @click-left="back"
        />
        <Bg>
            <van-tabs v-model="active" title-active-color="#0c34ba" color="#0c34ba">
                <van-tab title="全部"></van-tab>
                <van-tab title="进行中"></van-tab>
                <van-tab title="已完成"></van-tab>
            </van-tabs>
        </Bg>
        <div class="order-content" >
            <div class="going-box" v-show="active==0||active==1">
                <PayBox v-for="(item,index) in goingOrderData" :key="index" :sum="item[item.length-2]" :count="item[item.length-1]" :time="item[0].updatedAt|formatTime">
                    <template #title>
                        <div class="order-type-title">
                            <div>{{index}}</div>
                            <div class="sure" @click="confirm(index)">确认收货</div> 
                        </div>
                    </template>
                    <OrderItem v-for="(v,i) in item.slice(0,-2)" :key="i" :item="v"></OrderItem>
                </PayBox>
            </div>
            <div class="finish-box" v-show="active==0||active==2">
                <PayBox v-for="(item,index) in finishOrderData" :key="index" :sum="item[item.length-2]" :count="item[item.length-1]" :time="item[0].updatedAt|formatTime">
                    <template #title>
                        <div class="order-type-title">
                            <div>{{index}}</div>
                            <div class="finish">
                                <div>已完成</div>
                                <div class="trash-box">
                                    <van-icon name="delete-o" @click="deleteOrderData(index)" size="18" />
                                </div>
                            </div>
                        </div>
                    </template>
                    <OrderItem v-for="(v,i) in item.slice(0,-2)" :key="i" :item="v"></OrderItem>
                </PayBox>
            </div>
        </div>
    </div>
</template>

<script>
    import '../assets/less/myorders.less'
    import Bg from '../components/Bg'
    import PayBox from '../components/PayBox'
    import OrderItem from '../components/OrderItem'
    import {mapState,mapMutations} from 'vuex'

    export default {
        name:'MyOrder',
        data(){
            return {
                active:0,
                //进行中的订单数据
                goingOrderData:{},
                //已完成的订单数据
                finishOrderData:{}
            }
        },
        methods:{
            //返回
            back(){
                this.$router.push({name:'My'});
            },
            //获取订单信息
            getOrderData(){
                let tokenString = this.$cookies.get("tokenString");
                if(!tokenString) {
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                //发送登录请求
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findOrder',
                    params:{
                        appkey:this.appkey,
                        tokenString,
                        status:0
                    }
                }).then(res => {
                    //请求成功关闭加载提示
                    this.$toast.clear();
                    if(res.data.code == 70000){
                        //初始化
                        this.goingOrderData = {};
                        this.finishOrderData = {};
                        //分类
                        let data = res.data.result;
                        this.sortWithOrderData(data);
                    }
                }).catch(err => {
                    //请求失败
                    this.$toast({
                        message:err,
                        forbidClick:true,
                        duration:1000
                    })
                })
            },
            //处理订单数据
            sortWithOrderData(data){
                //根据订单分类
                for(let i = 0;i < data.length;i++){
                    data[i].small_img = data[i].smallImg;
                    if(data[i].status == 1) {
                        //属于同一个订单
                        if(this.goingOrderData[data[i].oid]){
                            this.goingOrderData[data[i].oid].push(data[i]);
                        }else {
                            //不同的订单,创建一个新数组，并保存数据
                            this.goingOrderData[data[i].oid] = [];
                            this.goingOrderData[data[i].oid].push(data[i]);
                        }
                    }
                    if(data[i].status == 2){
                        //属于同一个订单
                        if(this.finishOrderData[data[i].oid]){
                            this.finishOrderData[data[i].oid].push(data[i]);
                        }else {
                            //不同的订单,创建一个新数组，并保存数据
                            this.finishOrderData[data[i].oid] = [];
                            this.finishOrderData[data[i].oid].push(data[i]);
                        }
                    }
                }  
                //添加订单对应的商品数量与总价钱属性
                //进行中的订单
                for(let key in this.goingOrderData){
                    let sum = 0;
                    let count = 0;
                    for(let i = 0;i < this.goingOrderData[key].length;i++){
                        sum += parseFloat(this.goingOrderData[key][i].price)*this.goingOrderData[key][i].count;
                        count += this.goingOrderData[key][i].count;
                    }
                    this.goingOrderData[key].push(sum);
                    this.goingOrderData[key].push(count);
                }
                //已完成的订单
                for(let key in this.finishOrderData){
                    let sum = 0;
                    let count = 0;
                    for(let i = 0;i < this.finishOrderData[key].length;i++){
                        sum += parseFloat(this.finishOrderData[key][i].price)*this.finishOrderData[key][i].count;
                        count += this.finishOrderData[key][i].count;
                    }
                    this.finishOrderData[key].push(sum);
                    this.finishOrderData[key].push(count);
                }
            },
            //确认收货
            confirm(oid){
                let tokenString = this.$cookies.get("tokenString");
                if(!tokenString) {
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                //发送登录请求
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/receive',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        oid
                    }
                }).then(res => {
                    //请求成功关闭加载提示
                    this.$toast.clear();
                    if(res.data.code == 80000){
                        //更新已完成数据
                        this.finishOrderData[oid] = this.goingOrderData[oid];
                        //更新进行中数据(删除已收货数据)
                        this.$delete(this.goingOrderData,oid);
                        this.getOrderData();
                        this.$toast({
                            message:res.data.msg,
                            duration:1000,
                            forbidClick:true
                        })
                    }
                }).catch(err => {
                    //请求失败
                    this.$toast({
                        message:err,
                        forbidClick:true,
                        duration:1000
                    })
                })
            },
            //删除订单
            deleteOrderData(oid){
                let tokenString = this.$cookies.get("tokenString");
                if(!tokenString) {
                    return this.$router.push({name:'Login'});
                }
                //加载提示
                this.$toast.loading({
                    message: '删除中...',
                    forbidClick: true,
                    duration:0
                });
                //发送请求
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/removeOrder',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        oid
                    }
                }).then(res => {
                    //请求成功关闭加载提示
                    this.$toast.clear();
                    if(res.data.code == 90000){
                        //更新已完成数据
                        this.$delete(this.finishOrderData,oid);
                        this.$toast({
                            message:res.data.msg,
                            duration:1000,
                            forbidClick:true
                        })
                    }
                }).catch(err => {
                    //请求失败
                    this.$toast({
                        message:err,
                        forbidClick:true,
                        duration:1000
                    })
                })
            },
            ...mapMutations(["changeNewOrderMsg"])
        },
        components:{
            Bg,
            PayBox,
            OrderItem
        },
        created(){
            this.getOrderData();
            this.changeNewOrderMsg(false);
        },
        filters:{
            formatTime(value){
                let date = new Date(value);
                let year = date.getFullYear();
                let month = date.getMonth()+1;
                let day = date.getDate();
                let hours = date.getHours();
                let minutes = date.getMinutes();
                let seconds = date.getSeconds();
                if(month < 10){
                    month = '0' + month;
                }
                if(day < 10) {
                    day = '0' + day;
                }
                if(hours < 10) {
                    hours = '0' + hours;
                }
                if(minutes < 10) {
                    minutes = '0' + minutes;
                }
                if(seconds < 10) {
                    seconds = '0' + seconds;
                }
                return year + "-" + month + "-" + day + ' ' + hours + ':' +minutes + ":" + seconds;
                
            }
        },
        computed:{
            ...mapState(["isNewOrderMsg"])
        }
    }
</script>

