<template>
    <div class="pay-content-box">
        <slot name="title"></slot>
        <div class="item">
            <slot></slot>
        </div>
        <div class="total">
            <div class="time">{{time}}</div>
            <div class="total-bottom">
                <div class="total-number">共计 {{count}} 件</div>
                <div class="total-price">合计 &yen; {{sum|formatFloat}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    import "../assets/less/payBox.less"
    export default {
        name:"PayBox",
        props:["sum","count","time"],
        created(){
        },
        filters:{
            formatFloat(value){
                if(Number.isInteger(value)){
                    value = parseFloat(value).toFixed(2);
                }
                if(Number.isFinite(value)){
                    value = value.toFixed(2);
                }
                return value;
            }
        }
        
    }
</script>

